import { useState, useCallback, useMemo, useEffect, useRef, useLayoutEffect } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';
import { alphabetPracticeGroups, CORRECTION_KEY_NAME } from '../data/keymapData'; // CORRECTION_KEY_NAME をインポート

// 新しいステージを追加: 'capitalizeChar' (文字を大文字化して表示する中間ステージ)
// type CorrectionStage = 'groupInput' | 'charInput' | 'correctionInput' | 'capitalizeChar' | 'finished';
type CorrectionStage =
    | 'waitingForGroupKey'         // "abc" キー入力待ち
    | 'waitingForSmallCharKey'     // "①" キー入力待ち (小文字)
    | 'waitingForCorrectionKey'    // "訂正" キー入力待ち
    | 'finished';

const useCorrectionPractice = ({
    gIdx,
    dIdx,
    isActive,
    onAdvance,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [internalStage, setInternalStage] = useState<CorrectionStage>('waitingForGroupKey');
    const [currentTargetGroup, setCurrentTargetGroup] = useState<string>('');
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorHighlightDetails, setErrorHighlightDetails] = useState<{ layer: number; pressCode: number } | null>(null);

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);

    useEffect(() => {
        console.log('[CorrectionPractice layers prop]', layers);
    }, [layers]);
    const reset = useCallback(() => {
        setInternalStage('waitingForGroupKey');
        setCurrentTargetGroup('');
        setStatus('waiting');
        setErrorHighlightDetails(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
    }, [setInternalStage, setCurrentTargetGroup, setStatus, setErrorHighlightDetails]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        } else if (isActive) {
            if (gIdx < alphabetPracticeGroups.length) {
                const group = alphabetPracticeGroups[gIdx];
                setCurrentTargetGroup(group);
                setStatus('waiting');

                // gIdx または dIdx が変更された場合、またはアクティブになった直後にステージをリセット
                // Also reset error highlight when stage changes due to gIdx/dIdx change
                if (gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current || (isActive && !prevIsActiveRef.current)) {
                    setInternalStage('waitingForGroupKey');
                }

                if (dIdx >= group.length && group.length > 0) {
                    if (gIdx < alphabetPracticeGroups.length -1) {
                        onAdvance?.();
                    } else {
                        setStatus('finished');
                        setInternalStage('finished');
                    }
                }
            } else {
                setStatus('finished');
                setInternalStage('finished');
                setCurrentTargetGroup('');
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
    }, [gIdx, dIdx, isActive, reset, onAdvance, alphabetPracticeGroups, internalStage, setCurrentTargetGroup, setStatus, setInternalStage, setErrorHighlightDetails]);

    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        if (!isActive || internalStage === 'finished' || !currentTargetGroup || !layers) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
        }

        let currentErrorLayer: number | null = null;
        let currentErrorPressCode: number | undefined = undefined;

        if (input.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false };
        }

        const currentGroupKeyName = alphabetPracticeGroups[gIdx];
        const charInputKeyNames = ["①", "②", "③", "④"]; // 英字練習と同じ
        const expectedCharKeyName = (dIdx < charInputKeyNames.length) ? charInputKeyNames[dIdx] : null;

        console.log(`[CorrectionPractice handleInput] Stage: ${internalStage}, gIdx: ${gIdx}, dIdx: ${dIdx}, pressCode: 0x${input.pressCode.toString(16)}, currentTargetGroup: "${currentTargetGroup}"`);
        let pressedKeyNameOnLayer4: string | null = null;
        // input.pressCode は0始まりのインデックスなので、範囲チェックも0以上で行う
        if (layers[4] && input.pressCode >= 0 && input.pressCode < layers[4].length) {
            const rawKeyName = layers[4][input.pressCode];
            pressedKeyNameOnLayer4 = rawKeyName?.trim() ?? null;
            console.log(`[CorrectionPractice handleInput] Layer 4 Key Lookup: input.pressCode=${input.pressCode}, layers[4][${input.pressCode}]="${rawKeyName}", trimmed="${pressedKeyNameOnLayer4}"`);
        }

        let pressedKeyNameOnLayer5: string | null = null;
        if (layers[5] && input.pressCode >= 0 && input.pressCode < layers[5].length) {
            const rawKeyName = layers[5][input.pressCode];
            pressedKeyNameOnLayer5 = rawKeyName?.trim() ?? null;
            console.log(`[CorrectionPractice handleInput] Layer 5 Key Lookup: input.pressCode=${input.pressCode}, layers[5][${input.pressCode}]="${rawKeyName}", trimmed="${pressedKeyNameOnLayer5}"`);
        }

        if (internalStage === 'waitingForGroupKey') {
            if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4.toLowerCase() === currentGroupKeyName.toLowerCase()) {
                setInternalStage('waitingForSmallCharKey');
                setStatus('waiting');
                setErrorHighlightDetails(null);
                return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
            } else {
                currentErrorLayer = 4;
                currentErrorPressCode = input.pressCode;
            }
        } else if (internalStage === 'waitingForSmallCharKey') {
            if (expectedCharKeyName && pressedKeyNameOnLayer5 && pressedKeyNameOnLayer5 === expectedCharKeyName) {
                setInternalStage('waitingForCorrectionKey');
                setStatus('waiting');
                setErrorHighlightDetails(null);
                return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
            } else {
                currentErrorLayer = 5;
                currentErrorPressCode = input.pressCode;
            }
        } else if (internalStage === 'waitingForCorrectionKey') {
            if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4 === CORRECTION_KEY_NAME) {
                setStatus('correct');
                onAdvance?.(); // dIdx を進める
                // 次のステージは useEffect で dIdx/gIdx の変更を検知して waitingForGroupKey に設定される
                setErrorHighlightDetails(null);
                return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
            } else {
                currentErrorLayer = 4;
                currentErrorPressCode = input.pressCode;
            }
        }

        // If we reach here, it's an incorrect input
        setStatus('incorrect');
        setInternalStage('waitingForGroupKey'); // 不正解時は最初のステージに戻す
        setErrorHighlightDetails(currentErrorLayer !== null && currentErrorPressCode !== undefined ? { layer: currentErrorLayer, pressCode: currentErrorPressCode } : null);
        return { isExpected: false, shouldGoToNext: false, errorLayer: currentErrorLayer, errorPressCode: currentErrorPressCode };
    }, [isActive, internalStage, currentTargetGroup, gIdx, dIdx, onAdvance, layers, alphabetPracticeGroups, setInternalStage, setStatus, setErrorHighlightDetails]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        if (errorHighlightDetails && errorHighlightDetails.layer === layoutIndex) {
            return { className: null, overrideKey: null }; // Don't highlight if it's an error target
        }
        if (!isActive || internalStage === 'finished' || !currentTargetGroup) {
            return { className: null, overrideKey: null };
        }

        const currentGroupKeyNameForPractice = alphabetPracticeGroups[gIdx];
        const charInputKeyNamesForPractice = ["①", "②", "③", "④"];
        const expectedCharKeyNameForPractice = (dIdx < charInputKeyNamesForPractice.length) ? charInputKeyNamesForPractice[dIdx] : null;

        if (internalStage === 'waitingForGroupKey' && layoutIndex === 4 && keyName.trim().toLowerCase() === currentGroupKeyNameForPractice.toLowerCase()) {
            return { className: 'bg-blue-100 dark:bg-blue-700', overrideKey: keyName.toLowerCase() };
        } else if (internalStage === 'waitingForSmallCharKey' && layoutIndex === 5 && expectedCharKeyNameForPractice && keyName.trim() === expectedCharKeyNameForPractice) {
            return { className: 'bg-blue-100 dark:bg-blue-700', overrideKey: null };
        } else if (internalStage === 'waitingForCorrectionKey' && layoutIndex === 4 && keyName.trim() === CORRECTION_KEY_NAME) {
            return { className: 'bg-blue-100 dark:bg-blue-700', overrideKey: null };
        }
        return { className: null, overrideKey: null };
    }, [isActive, internalStage, currentTargetGroup, gIdx, dIdx, alphabetPracticeGroups, errorHighlightDetails]);

    const headingChars = useMemo(() => {
        // "a-A b-B c-C" 形式で生成
        if (gIdx < alphabetPracticeGroups.length) {
            const group = alphabetPracticeGroups[gIdx];
            return group.split('').map(char => `${char.toLowerCase()}-${char.toUpperCase()}`);
        }
        return [];
    }, [gIdx, alphabetPracticeGroups]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightDetails(null);
        setStatus('waiting');
    }, [setErrorHighlightDetails, setStatus]);

    return {
        status,
        handleInput,
        getHighlightClassName,
        targetChar: currentTargetGroup && dIdx < currentTargetGroup.length
            ? (internalStage === 'waitingForCorrectionKey' ? currentTargetGroup[dIdx].toUpperCase() : currentTargetGroup[dIdx].toLowerCase())
            : undefined,
        reset,
        targetLayerIndex: null,
        headingChars,
        targetLayerIndices: [4, 5],
        isInvalidInputTarget: (targetPressCode, currentLayoutIndex, currentKeyIndex, pressedLayerIndex) => {
            if (!isActive) return false;
            if (!errorHighlightDetails) return false;
            return currentLayoutIndex === errorHighlightDetails.layer && currentKeyIndex === targetPressCode;
        },
        errorHighlightLayer: errorHighlightDetails?.layer ?? null,
        clearErrorState,
    };
};

export default useCorrectionPractice;
