// /home/coffee/my-keymap-viewer/src/hooks/useSeionPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHighlightResult,
    hid2Gyou,
    hid2Dan,
} from './usePracticeCommons';
import { gyouList, danOrder, gyouChars, danList } from '../data/keymapData';

type SeionStage = 'gyouInput' | 'danInput';

const useSeionPractice = ({
    gIdx: initialGIdx,
    dIdx: initialDIdx,
    isActive,
    side,
    layers, // layers は handleInput で使うので必要
    kb,
    isRandomMode,
}: PracticeHookProps): PracticeHookResult => {
    // gIdx, dIdx は props から受け取った値を初期値として内部状態を持つ
    const [gIdx, setGIdx] = useState(initialGIdx);
    const [dIdx, setDIdx] = useState(initialDIdx);

    const [targetChar, setTargetChar] = useState<string | null>(null); // This will store the actual character to be highlighted, e.g., "か", "き"
    const [stage, setStage] = useState<SeionStage>('gyouInput');
    const [randomTarget, setRandomTarget] = useState<{ char: string; gyouKey: string; danKey: string } | null>(null);    
    const prevGIdxRef = useRef(gIdx);
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null); // 例: エラーレイヤー管理
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    
    // --- 期待されるキー名の計算 ---
    const expectedGyouKey = useMemo(() => {
        if (!isActive) return null;
        if (isRandomMode) return randomTarget?.gyouKey ?? null;
        // 通常モード: 内部状態の gIdx を使用
        if (gIdx < 0 || gIdx >= gyouList.length) return null;
        return gyouList[gIdx];
    }, [isActive, isRandomMode, randomTarget, gIdx]); // 内部状態の gIdx に依存

    const expectedDanKey = useMemo(() => {
        if (!isActive) return null;
        if (isRandomMode) return randomTarget?.danKey ?? null;
        // 通常モード: 内部状態の gIdx, dIdx を使用
        if (gIdx < 0 || gIdx >= gyouList.length) return null;
        const currentGyouKey = gyouList[gIdx];
        // ▼▼▼ や行の特別処理 ▼▼▼
        if (currentGyouKey === 'や行') {
            if (dIdx === 0) return 'あ段'; // や
            if (dIdx === 1) return 'う段'; // ゆ
            if (dIdx === 2) return 'お段'; // よ
            return null; // や行にはこれ以外の dIdx はないはず
        }
        // ▲▲▲ や行の特別処理 ▲▲▲
        const list = danOrder[currentGyouKey]; // ['あ', 'い', 'う', 'え', 'お'] など
        const charsForGyou = gyouChars[currentGyouKey]; // ['あ', 'い', 'う', 'え', 'お'] など
        if (!list || !charsForGyou || dIdx < 0 || dIdx >= list.length || dIdx >= charsForGyou.length) return null;
        const char = charsForGyou[dIdx]; // dIdxから文字を取得
        const charIndex = list.findIndex(c => c === char); // danOrderで文字のindexを探す
        if (charIndex === -1 || charIndex >= danList.length) return null;
        return danList[charIndex]; // indexからdanListで段名を取得
    }, [isActive, isRandomMode, randomTarget, gIdx, dIdx]); // 内部状態の gIdx, dIdx に依存

    // --- ターゲット文字と対応キーの計算ロジック ---
    // This logic will be moved into useEffect or a function called by useEffect
    // that directly uses the current gIdx, dIdx from state.
    const updateTargetAndStage = useCallback(() => {
        console.log(`[SeionPractice updateTargetAndStage] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx(state): ${gIdx}, dIdx(state): ${dIdx}`);

        if (isRandomMode) {
            const allSeion: { char: string; gyouKey: string; danKey: string }[] = [];
            gyouList.forEach((gyouKey) => {
                const actualCharsInGyou = gyouChars[gyouKey]; // 実際の文字リストを取得 (例: や行なら ['や', 'ゆ', 'よ'])
                if (actualCharsInGyou) {
                    actualCharsInGyou.forEach((char) => { // 実際の文字でループ
                        let determinedDanKey: string | null = null;
                        if (gyouKey === 'や行') {
                            // や行の特殊マッピング
                            if (char === 'や') determinedDanKey = 'あ段';
                            else if (char === 'ゆ') determinedDanKey = 'う段';
                            else if (char === 'よ') determinedDanKey = 'お段';
                        } else {
                            // や行以外：danOrderでの文字の位置からdanListのインデックスを取得して段キーを決定
                            const charDisplayIndex = danOrder[gyouKey]?.indexOf(char);
                            if (charDisplayIndex !== -1 && charDisplayIndex !== undefined && charDisplayIndex < danList.length) {
                                determinedDanKey = danList[charDisplayIndex];
                            }
                        }

                        if (determinedDanKey) {
                            allSeion.push({ char, gyouKey, danKey: determinedDanKey });
                        }
                    });
                }
            });

            if (allSeion.length > 0) {
                const randomIndex = Math.floor(Math.random() * allSeion.length);
                const newTarget = allSeion[randomIndex];
                setRandomTarget(newTarget);
                setTargetChar(newTarget.char); // Set targetChar to the actual practice character, e.g., "か"
                setStage('gyouInput');
                console.log(`[SeionPractice updateTargetAndStage RANDOM] Set randomTarget: ${newTarget.char}, targetChar: ${newTarget.char}`);
                // console.log(`[Seion Random] New target: ${newTarget.char} (Gyou: ${newTarget.gyouKey}, Dan: ${newTarget.danKey})`);
            } else {
                console.error("[Seion Random] No seion characters found.");
                setRandomTarget(null);
                setTargetChar(null);
            }
        } else {
            // 通常モード: 内部状態の gIdx, dIdx を使用
            if (gIdx < 0 || gIdx >= gyouList.length) {
                setTargetChar(null);
                setRandomTarget(null);
                setStage('gyouInput');
                return;
            }
            const currentGyouKey = gyouList[gIdx];
            const list = danOrder[currentGyouKey];
            const charsForGyou = gyouChars[currentGyouKey];

            if (!list || !charsForGyou || dIdx < 0 || dIdx >= list.length || dIdx >= charsForGyou.length) {
                setTargetChar(null);
                setRandomTarget(null);
                setStage('gyouInput');
                return;
            }
            const char = charsForGyou[dIdx]; // The actual character for practice, e.g., "か"
            const charIndex = list.findIndex(c => c === char);
            const danKey = (charIndex !== -1 && charIndex < danList.length) ? danList[charIndex] : null;

            if (char && danKey) {
                setTargetChar(char); // Set targetChar to the actual practice character
                setRandomTarget(null); // 通常モードではクリア
                setStage('gyouInput');
                console.log(`[SeionPractice updateTargetAndStage NORMAL] Set targetChar: ${char} for gIdx=${gIdx}, dIdx=${dIdx}`);
                // console.log(`[Seion Normal] Target: ${char} (Gyou: ${currentGyouKey}, Dan: ${danKey}) for gIdx=${gIdx}, dIdx=${dIdx}`);
            } else {
                 setTargetChar(null);
                 setRandomTarget(null);
                 setStage('gyouInput');
            }
        }
    }, [isActive, gIdx, dIdx, isRandomMode]); // Removed setRandomTarget, setTargetChar, setStage from deps as they are called within

    // --- ターゲット更新 ---
    useEffect(() => {
        // Sync props to internal state first
        if (initialGIdx !== gIdx) setGIdx(initialGIdx);
        if (initialDIdx !== dIdx) setDIdx(initialDIdx);
    }, [initialGIdx, initialDIdx, gIdx, dIdx]); // Only sync when props or internal state differ

    useEffect(() => {
        console.log(`[SeionPractice useEffect MainLogic] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx(state): ${gIdx}, dIdx(state): ${dIdx}, isInitialMount: ${isInitialMount.current}, prevIsRandomMode: ${prevIsRandomModeRef.current}, prevIsActive: ${prevIsActiveRef.current}`);

        if (!isActive) {
            if (prevIsActiveRef.current) { // false になった瞬間だけリセット
                console.log("[SeionPractice useEffect MainLogic] Practice became inactive. Resetting state.");
                setTargetChar(null);
                setStage('gyouInput');
                setRandomTarget(null);
                setCurrentErrorHighlightLayer(null);
                prevGIdxRef.current = -1;
                prevDIdxRef.current = -1;
                isInitialMount.current = true;
            }
            prevIsActiveRef.current = isActive;
            prevIsRandomModeRef.current = isRandomMode;
            return;
        }

        // --- Practice is Active ---
        const justActivated = !prevIsActiveRef.current && isActive;
        if (justActivated) {
            isInitialMount.current = true;
        }

        const internalIndicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
        const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

        // Determine if target update is needed
        let needsTargetUpdate = false;
        if (isRandomMode) {
            if (randomModeChanged || isInitialMount.current || !randomTarget) {
                needsTargetUpdate = true;
                console.log(`[SeionPractice useEffect MainLogic] RANDOM mode. Needs target update. randomModeChanged=${randomModeChanged}, isInitialMount=${isInitialMount.current}, !randomTarget=${!randomTarget}`);
            }
        } else { // Normal Mode
            if (randomModeChanged || isInitialMount.current || internalIndicesChanged) {
                needsTargetUpdate = true;
                console.log(`[SeionPractice useEffect MainLogic] NORMAL mode. Needs target update. randomModeChanged=${randomModeChanged}, isInitialMount=${isInitialMount.current}, internalIndicesChanged=${internalIndicesChanged}`);
            }
        }

        if (needsTargetUpdate) {
            updateTargetAndStage(); // This uses internal gIdx, dIdx
            setCurrentErrorHighlightLayer(null); // Clear error on target update
        }

        // Update refs for the next run
        if (needsTargetUpdate || justActivated) { // Only update if a change occurred that might reset initialMount
            isInitialMount.current = false;
        }
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
    }, [isActive, gIdx, dIdx, isRandomMode, randomTarget, updateTargetAndStage]);


    // --- 入力処理 ---
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !targetChar || !expectedGyouKey || !expectedDanKey || inputInfo.type !== 'release') {
            setCurrentErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        const { pressCode } = inputInfo;
        let isExpected = false;
        let errorLayer: number | null = null;
        let shouldGoToNext: boolean | undefined = undefined; // 初期値を undefined に変更

        const actualGyouKey = hid2Gyou(pressCode + 1, kb, side); // 0-based の pressCode を 1-based に変換
        const actualDanKey = hid2Dan(pressCode + 1, kb, side);   // 0-based の pressCode を 1-based に変換

        if (stage === 'gyouInput') {
            if (actualGyouKey === expectedGyouKey) {
                isExpected = true;
                setStage('danInput');
                setCurrentErrorHighlightLayer(null); // Correct gyou input, clear error
                errorLayer = null;
                shouldGoToNext = undefined; // 文字入力はまだ完了していない
            } else {
                isExpected = false;
                // Stage remains 'gyouInput'
                setCurrentErrorHighlightLayer(2); // Error on gyouInput (layer 2)
                errorLayer = 2;
                shouldGoToNext = undefined; // 文字入力は完了しておらず、不正解
            }
        } else if (stage === 'danInput') {
            if (actualDanKey === expectedDanKey) {
                isExpected = true;
                setStage('gyouInput');
                setCurrentErrorHighlightLayer(null); // Correct dan input, clear error
                errorLayer = null;
                if (isRandomMode) {
                    updateTargetAndStage(); // 次のランダムターゲットを計算
                    shouldGoToNext = false; // ランダムモードでは App.tsx は dIdx を進めるが、フック内部で次のターゲットを処理
                } else {
                    // 通常モード: 現在の文字がその行の最後かどうかを判定
                    const currentGyouKeyFromProps = gyouList[initialGIdx]; // props の gIdx を使用
                    const charsInCurrentGyou = gyouChars[currentGyouKeyFromProps];
                    if (charsInCurrentGyou && initialDIdx >= charsInCurrentGyou.length - 1) {
                        shouldGoToNext = true; // 行の最後の文字なら App.tsx は gIdx を進める
                    } else {
                        shouldGoToNext = false; // 行の途中の文字なら App.tsx は dIdx を進める
                    }
                }
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(3); // Error on danInput (layer 3)
                setStage('gyouInput'); // Reset to gyouInput on error
                errorLayer = 3;
                shouldGoToNext = undefined; // 文字入力は完了しておらず、不正解
            }
        }
        console.log(`[SeionPractice handleInput] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext}, errorLayer=${errorLayer}`);
        return { isExpected, shouldGoToNext, errorLayer };
    }, [isActive, targetChar, stage, expectedGyouKey, expectedDanKey, kb, side, isRandomMode, updateTargetAndStage, initialGIdx, initialDIdx, setStage, setCurrentErrorHighlightLayer]);

    // --- リセット処理 (App.tsx から呼ばれる想定) ---
    const reset = useCallback(() => {
        setStage('gyouInput');
        setCurrentErrorHighlightLayer(null);
        // Target will be recalculated by the main useEffect if isActive
    }, []);

    // --- headingChars ---
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        if (isRandomMode) {
            const result = targetChar ? [targetChar] : [];
            console.log(`[SeionPractice headingChars RANDOM] Result:`, result);
            return result;
        } else {
            // 通常モード: 内部状態の gIdx を使用
            if (gIdx < 0 || gIdx >= gyouList.length) return [];
            const currentGyouKey = gyouList[gIdx];
            const charsForGyou = gyouChars[currentGyouKey];
            const result = charsForGyou ?? [];
            console.log(`[SeionPractice headingChars NORMAL] GyouKey: ${currentGyouKey}, Result:`, result);
            return result;
        }
    // 内部状態の gIdx と targetChar (ランダムモード用) に依存
    }, [isActive, isRandomMode, targetChar, gIdx]);

    // --- ハイライト処理 ---
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };

        if (!isActive || !targetChar) {
            return noHighlight;
        }

        // If an error highlight is active for this layer, don't show the normal "expected key" highlight.
        // The error itself will be highlighted by PracticeView using isInvalidInputTarget.
        if (currentErrorHighlightLayer === layoutIndex) {
            return noHighlight;
        }

        // Use current stage directly, as useEffect handles stage reset on target change
        const currentStageForHighlight = stage;

        if (currentStageForHighlight === 'gyouInput') {
            if (keyName === expectedGyouKey && layoutIndex === 2) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        } else if (currentStageForHighlight === 'danInput') {
            // For 'や行', expectedDanKey is already correctly 'あ段', 'う段', or 'お段'.
            // The check `expectedGyouKey === 'や行'` is redundant here if expectedDanKey is derived correctly.
            if (keyName === expectedDanKey && layoutIndex === 3) {
                 return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return noHighlight;
    }, [isActive, targetChar, stage, expectedGyouKey, expectedDanKey, currentErrorHighlightLayer]); // Removed isRandomMode, gIdx, dIdx as they are covered by expectedKeys or stage

    // --- 不正入力ターゲット判定 ---
    // targetPressCode is the 0-based index of the key that was actually pressed.
    // currentLayoutIndex is the layer being rendered.
    // currentKeyIndex is the 0-based index of the key on currentLayoutIndex being rendered.
    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        if (!isActive) return false; // 非アクティブ時は常に false

        // If an error highlight is active for the currentLayoutIndex,
        // and the currentKeyIndex matches the targetPressCode (the erroneously pressed key),
        // then this key is the one to be highlighted as an error.
        if (currentErrorHighlightLayer === currentLayoutIndex && currentKeyIndex === targetPressCode) {
            return true;
        }
        return false;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, [setCurrentErrorHighlightLayer]);

    // --- フックの戻り値 ---
    return useMemo(() => {
        // Determine targetLayerIndices based on currentErrorHighlightLayer
        let determinedTargetLayerIndices = [2, 3]; // Default for Seion
        if (currentErrorHighlightLayer !== null) {
            // Add the error layer to the set of layers to display, ensuring no duplicates
            const layersToShow = new Set([2, 3]); // Start with default layers
            layersToShow.add(currentErrorHighlightLayer); // Add error layer
            determinedTargetLayerIndices = Array.from(layersToShow);
        }

        return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        targetChar: targetChar ?? '', // This will be the actual character to be highlighted, e.g., "か", "き"
        getHighlight: () => { // 基本的な実装
            let start: string | null = null;
            let end: string | null = null;
            if (stage === 'gyouInput') start = expectedGyouKey;
            else if (stage === 'danInput') end = expectedDanKey;
            return { start, end };
        },
        targetLayerIndices: determinedTargetLayerIndices, // Modified
        errorHighlightLayer: currentErrorHighlightLayer, // Added
        clearErrorState,
        stage, // Added for PracticeManagement dependency array
        };
    }, [
        headingChars,
        handleInput, getHighlightClassName, reset, isInvalidInputTarget,
        targetChar, stage, currentErrorHighlightLayer, clearErrorState // Removed expectedGyouKey, expectedDanKey as they are implicitly covered by other deps
    ]);
};

export default useSeionPractice;
